









































import {Vue} from 'vue-property-decorator';
import {Loading} from 'quasar';
import cfg from "../heating-config";
import axios from 'axios';

require('url-search-params-polyfill');

export default Vue.extend({
    data() {
        return {
            state: false,
            outputPins: [],
            inputPins: []
        }
    },
    methods: {
        loadCurrentState() {
            Loading.show();
            axios.get(cfg.BASE_URL + "serviceMode")
                .then(response => {
                    this.state = response.data.state;
                    Loading.hide();
                })
                .catch(error => {
                    Loading.hide();
                    alert(error)
                });
            axios.get(cfg.BASE_URL + "port/outputs")
                .then(response => {
                    this.outputPins.splice(0);
                    for (let i = 0; i < response.data.length; i++)
                        this.outputPins.push({
                            'refcd': response.data[i].refcd,
                            'name': response.data[i].name,
                            'value': response.data[i].value === 'true'
                        });
                    Loading.hide();
                })
                .catch(error => {
                    Loading.hide();
                    alert(error)
                });
            axios.get(cfg.BASE_URL + "port/inputs")
                .then(response => {
                    this.inputPins.splice(0);
                    for (let i = 0; i < response.data.length; i++)
                        this.inputPins.push({
                            'refcd': response.data[i].refcd,
                            'name': response.data[i].name,
                            'value': response.data[i].value === 'true'
                        });
                    Loading.hide();
                })
                .catch(error => {
                    Loading.hide();
                    alert(error)
                });
        },
        handleStateChange() {
            Loading.show();
            axios.post(cfg.BASE_URL + "serviceMode?state=" + this.state)
                .then(response => {
                    this.state = response.data.state;
                    Loading.hide();
                })
                .catch(error => {
                    Loading.hide();
                    alert(error)
                });
        },
        toggleOutputState(output: any) {
            Loading.show();
            axios.post(cfg.BASE_URL + "port/output/" + output.refcd, output.value, {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
                .then(response => {
                    this.state = response.data.state;
                    Loading.hide();
                })
                .catch(error => {
                    Loading.hide();
                    alert(error)
                });
        }
    },
    watch: {
        state: function () {
            this.handleStateChange();
        }
    },
    mounted(): void {
        this.loadCurrentState();
    }
})
